<template>
  <div class="pos">
    <el-row>
      <el-col :span="7" class="pos-order" id="order-list">
        <el-tabs type='card'>
          <el-tab-pane label="点餐">
            <el-table :data="tableData" border style="width:100%;">
              <el-table-column prop="goodsName" label="商品名称"></el-table-column>
              <el-table-column prop="count" label="数量" width="50"></el-table-column>
              <el-table-column prop="price" label="金额" width="50"></el-table-column>
              <el-table-column label="操作" width="100" fixed="right">
                <template slot-scope="scope">
                  <el-button type="text" size="small" @click="delSingleGoods(scope.row)">删除</el-button>
                  <el-button type="text" size="small" @click="addOrderList(scope.row)">添加</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="totalDiv">
              <small>数量：{{totalCount}} 金额：{{totalMoney}}元 </small>
            </div>
            <div class="div-btn">
              <el-button type="warning">挂单
              </el-button>
              <el-button type="danger" @click="delAllGoods()">删除</el-button>
              <el-button type="success" @click="checkout()">结账</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane label="挂单">挂单</el-tab-pane>
          <el-tab-pane label="外卖">外卖</el-tab-pane>
        </el-tabs>
      </el-col>

      <el-col :span="17" >
          <div class="often-goods">
            <div class="title">常用商品</div>
          <div class="often-goods-list">
            <ul>
              <li v-for="(goods,index) in oftenGoods" :key="index" @click="addOrderList(goods)">
                <span>{{goods.goodsName}}</span>
                <span class="o-price">￥{{goods.price}}</span>
              </li>
            </ul>
          </div>
          </div>

          <div class="goods-type">
            <el-tabs >
              <el-tab-pane label="汉堡">
                <div>
                  <ul class="cookList">
                    <li v-for="(goods,index) in type0Goods" :key="index" @click="addOrderList(goods)">
    <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
    <span class="foodName">{{goods.goodsName}}</span>
    <span class="foodPrice">￥{{goods.price}}元</span>
</li>
                  </ul>
                </div>
              </el-tab-pane>
              <el-tab-pane label="小食">
                小食
              </el-tab-pane>
              <el-tab-pane label="饮料">
                饮料
              </el-tab-pane>
              <el-tab-pane label="套餐">
                套餐
              </el-tab-pane>
            </el-tabs>
          </div>
      </el-col>


      
    </el-row>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'pos',
  data(){
    return {
      tableData: [],
        oftenGoods:[],
        type0Goods:[],
        type1Goods:[],
        type2Goods:[],
        type3Goods:[],
        totalMoney:0,
        totalCount:0
    }
  },
  created(){
    // this.testName = '1';

    // console.log(this.testName);
    axios.get('https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/oftenGoods').then(response =>{
          this.oftenGoods=response.data;
          this.type0Goods=response.data[0];
          this.type1Goods=response.data[1];
          this.type2Goods=response.data[2];
          this.type3Goods=response.data[3];
      console.log(response);
    }).catch(error=>{
      console.log(error);
    });
  },
  mounted(){
    var orderHeight = document.body.clientHeight;
    console.log(orderHeight)
    document.getElementById("order-list").style.height = orderHeight + 'px';
    
      // Object.defineProperty(this,'testName',{
      //   get(){
      //     console.log(this.testName);
      //   },set(newValue){
      //     this.testName = newValue;
      //   }
      // });
      // this.testName;
      // this.testName = '123132';
  },methods:{
    addOrderList(goods){
      // this.totalMoney = 0;
      // this.totalCount = 0;
      //商品是否存在于订单列表
      let isHave = false;
      for(let i = 0;i<this.tableData.length;i++){
        if(this.tableData[i].goodsId==goods.goodsId){
          isHave = true;
        }
      }
      
      //根据判断的值编写业务逻辑
      if(isHave){
        //改变列表商品的数量
        let arr = this.tableData.filter(a=>a.goodsId == goods.goodsId);
        arr[0].count++;
      }else{
        let newGoods = {goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1};
        this.tableData.push(newGoods);
      }
      this.getAllMoney();

      
    },
    //删除单个商品
    delSingleGoods(goods){
      this.tableData = this.tableData.filter(o=>o.goodsId != goods.goodsId);
      this.getAllMoney();
    },
    delAllGoods(){
      this.tableData = [];
      this.totalMoney = 0;
      this.totalCount = 0;
    },
    //模拟结账
    checkout(){
      if(this.totalCount !=0){
        this.tableData = [];
        this.totalMoney = 0;
        this.totalCount = 0;
        this.$message({
          message:'结账成功',
          type:'success'
        });
      }else{
        this.$message.error('未选择任何商品，无法结账');
      }
    },
    //汇总数量金额
    getAllMoney(){
        this.totalCount = 0;
        this.totalMoney = 0;
      if(this.tableData){
        this.tableData.forEach((element)=> {
        
        this.totalCount +=element.count;
        this.totalMoney=this.totalMoney+(element.price*element.count);
      });
      }
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.pos-order{
  background-color: #F9FAFC;
  border-right: 1px solie #C0CCDA;
}
.div-btn{
  margin-top: 10px
}
.title{
  height: 20px;
  border-bottom: 1px solid #D3dce6;
  background-color: white;
  padding: 10px;
  text-align: center;
}
.often-goods-list ul li{
  cursor: pointer;
  list-style: none;
  float: left;
  border: 1px solid #E5E9F2;
  background-color: white;
  padding: 10px;
  margin: 10px;
}
.o-price{
  color: #58b7ff;
}
.goods-type{
  clear: both;
  cursor: pointer;
  
}
.cookList li{
       list-style: none;
       width:23%;
       border:1px solid #E5E9F2;
       height: auot;
       overflow: hidden;
       background-color:#fff;
       padding: 2px;
       float:left;
       margin: 2px;
   }
   .cookList li span{
        display: block;
        float:left;
   }
   .foodImg{
       width: 40%;
   }
   .foodName{
       font-size: 18px;
       padding-left: 10px;
       color:brown;
   }
   .foodPrice{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
   }
   .totalDiv{
     background-color: #fff;
     padding: 10px;
     border-bottom: 1px solid #D3dce6;
   }
</style>



